import React, { useEffect } from 'react';
import styles from './frame.css';
import config from '../../config';
import Head from 'next/head';
import classnames from 'classnames';
import Footer from '../../sections/footer/footer';
import AnimatedBackground from './background';
import { Logo } from '../logo';

type Props = {
  singlePage?: boolean;
  title: string;
  ogImage?: string;
  description: string;
  children: any;
};

let loaded;

export default function Frame(p: Props) {
  useEffect(() => {
    if (!loaded) {
      loaded = true;
      const wfPromise = import('webfontloader');
      wfPromise
        .then((WebFont) => {
          WebFont.load({
            typekit: {
              id: 'uaf4juj',
            },
          });
        })
        .catch((err) => console.error(err));
    }
  });

  const title = `${p.title} ðŸš€ ${config.title}`;

  return (
    <div
      className={classnames(styles.frame, p.singlePage && styles.singlePage)}
    >
      <Head>
        <title>{title}</title>
        <link rel="shortcut icon" href="/static/favicon.ico" />
        <meta name="description" content={p.description} />
        <meta property="og:description" content={p.description} />
        <meta property="og:title" content={p.title} />
        <meta property="og:image" content="/static/open-graph.png" />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        <meta property="og:url" content={config.baseUrl} />
        <meta property="og:site_name" content={config.title} />
        <meta name="twitter:card" content="photo" />
        <meta name="twitter:site" content={config.twitterName} />
        <meta name="twitter:image:width" content="750" />
        <meta name="twitter:description" content={p.description} />
        <meta name="twitter:image:height" content="560" />
      </Head>
      <a className={styles.logoContainer} href="/">
        <div className={styles.logo}>
          <Logo />
        </div>
      </a>
      {p.children}
      <Footer />
      <AnimatedBackground />
    </div>
  );
}
